.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;

  @media screen and (max-width: 767.98px) {
    max-width: 480px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279.98px) {
    width: 664px;
  }

  @media screen and (min-width: 1280px) {
    width: 852px;
  }
}

.container_max {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
}
